import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { RuleExeConfig } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-exe-config/bean/rule-exe-config.bean";
import { specialSymbols,checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'rule-exe-config',
    templateUrl: './rule-exe-config.component.html',
    styleUrls: ['./rule-exe-config.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class RuleExeConfigComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    list_Table: any = [];//表格数据
    ActionTypeList:any = [];//规则行为名称码值
    ProductCondList:any = [];//条件维度名称码值
    fromStartDate: any;//生效日期起
    fromEndDate: any;//	生效日期止
    thruStartDate: any;//	失效日期起
    thruEndDate: any;//	失效日期止
    state:any = [];//状态码值
    index: any = '0';//是否初始化查询
    EnAndNumShow: Boolean = false;//规则执行编号校验
    ChineseShow: Boolean = false;//规则执行名称校验
    ruleexeconfig:RuleExeConfig = new RuleExeConfig();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.selectProductPriceActionTypeNameList();
        this.selectProductCondNameList();
        this.codeVal();
        this.state = [{ label: '请选择', value: null }, { label: '录入待复核', value: '0' }, { label: '复核生效', value: '1' }, { label: '修改待复核', value: '2' },
        { label: '作废待复核', value: '3' }, { label: '作废', value: '4' }, { label: '复核退回', value: '5' }];//状态
        this.index = this.route.snapshot.params['query'];
        if(this.index){
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //规则行为名称码值
    selectProductPriceActionTypeNameList(){
        let params = {
        }
        this.httpService.selectProductPriceActionTypeNameList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List=[];
                List.push({ "label":'请选择', "value": null });
                data.productPriceActionTypeList.forEach((v) => {
                    List.push({ "label": v.priceRuleActionName, "value": v.priceRuleActionCode});
                })
                this.ActionTypeList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则行为名称查询失败', detail: '调用服务失败！' });
        })
    }
    //条件维度名称码值
    selectProductCondNameList(){
        let params = {
        }
        this.httpService.selectProductCondNameList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List=[];
                List.push({ "label":'请选择', "value": null });
                data.enumerationTypeList.forEach((v) => {
                    List.push({ "label": v.conditionalDimensionName, "value": v.conditionalDimensionCode});
                })
                this.ProductCondList = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '条件维度名称查询失败', detail: '调用服务失败！' });
        })
    }
    //查询
    query(){
        if(this.EnAndNumShow || this.ChineseShow){
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectPriceRuleExecuteInfoList();
    }
    selectPriceRuleExecuteInfoList(){
        let params = {
            priceRuleExecuteName: this.ruleexeconfig.priceRuleExecuteName,	// 规则执行名称
            priceRuleExecuteCode: this.ruleexeconfig.priceRuleExecuteCode,	// 	规则执行编号
            priceRuleActionName: this.ruleexeconfig.priceRuleActionName,	// 	规则行为名称
            priceRuleActionCode: this.ruleexeconfig.priceRuleActionCode,	//	条件行为编号
            conditionalDimensionCode: this.ruleexeconfig.conditionalDimensionCode,	//	条件维度编号            
            conditionalDimensionName: this.ruleexeconfig.conditionalDimensionName,	// 	条件维度名称
            statusCode: this.ruleexeconfig.statusCode,//状态
            fromStartDate: this.commfunc.transDateN(this.fromStartDate),//生效日期起
            fromEndDate: this.commfunc.transDateN(this.fromEndDate),//	生效日期止
            thruStartDate: this.commfunc.transDateN(this.thruStartDate),//	失效日期起
            thruEndDate: this.commfunc.transDateN(this.thruEndDate),//	失效日期止
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectPriceRuleExecuteInfoList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                 this.list_Table = data.productPriceRuleExecuteList;
                 for(let l=0; l<this.list_Table.length; l++){
                    this.list_Table[l].fromDate = this.commfunc.transDateN(this.list_Table[l].fromDate);
                    this.list_Table[l].thruDate = this.commfunc.transDateN(this.list_Table[l].thruDate);
                }
                 this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则执行配置查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则执行配置查询失败', detail: '调用服务失败！' });
            })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectPriceRuleExecuteInfoList();
    }
    //重置
    reset(){
        this.ruleexeconfig =  new RuleExeConfig();
        this.fromStartDate='';//生效日期起
        this.fromEndDate='';//	生效日期止
        this.thruStartDate='';//	失效日期起
        this.thruEndDate=''//	失效日期止
        this.EnAndNumShow = false;
        this.ChineseShow = false;
        this.query();
    }
     //生、失效起止日期大小比较
     selectTime(a) {
        if (this.fromStartDate && this.fromEndDate) {
            if (this.fromStartDate >= this.fromEndDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于生效日期止日' });
                if (a==1) {
                    setTimeout(() => {
                        this.fromStartDate = null;
                    })

                } else if(a==2){
                    setTimeout(() => {
                        this.fromEndDate = null;
                    })
                }
            }
        }
        if (this.thruStartDate && this.thruEndDate) {
            if (this.thruStartDate >= this.thruEndDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '失效日期起日不能大于失效日期止日' });
                if (a==3) {
                    setTimeout(() => {
                        this.thruStartDate = null;
                    })

                } else if(a==4){
                    setTimeout(() => {
                        this.thruEndDate = null;
                    })
                }
            }
        }
        if (this.fromStartDate && this.thruStartDate) {
            if (this.fromStartDate >= this.thruStartDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于失效日期起日' });
                if (a==1) {
                    setTimeout(() => {
                        this.fromStartDate = null;
                    })

                } else if(a==3){
                    setTimeout(() => {
                        this.thruStartDate = null;
                    })
                }
            }
        }
        if (this.fromStartDate && this.thruEndDate) {
            if (this.fromStartDate >= this.thruEndDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于失效日期止日' });
                if (a==1) {
                    setTimeout(() => {
                        this.fromStartDate = null;
                    })

                } else if(a==4){
                    setTimeout(() => {
                        this.thruEndDate = null;
                    })
                }
            }
        }
    }
    //汉字校验
    onChineseCheck() {
        if (this.ruleexeconfig.priceRuleExecuteName != '') {
            if (specialSymbols(null, this.ruleexeconfig.priceRuleExecuteName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else{
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.ruleexeconfig.priceRuleExecuteCode != '') {
            if (checkEnAndNum(null, this.ruleexeconfig.priceRuleExecuteCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else{
            this.EnAndNumShow = false;
        }
    }
    //删除
    toDelete(col){
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    priceRuleExecuteName: col.priceRuleExecuteName, //规则执行名称
                    priceRuleExecuteCode: col.priceRuleExecuteCode, //	规则执行编号
                    priceRuleActionSeqCode: col.priceRuleActionSeqCode, //规则行为序列编号
                    conditionalDimensionList: col.conditionalDimensionList, //条件维度明细列表
                };
                this.httpService.removePriceRuleExecuteInfo(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
    //新增
    addBtn() {
        this.router.navigate(['/pages/tzb/valuation/rule/rule-exe-config-update', { title: '规则执行配置新增'}])
    }
    //修改
    toUpdate(col) {
        this.router.navigate(['/pages/tzb/valuation/rule/rule-exe-config-update', { title: '规则执行配置修改', dataCode: col.priceRuleExecuteCode, dataName: col.priceRuleExecuteName, }])
    }
    //详情
    toDetail(col) {
        this.router.navigate(['/pages/tzb/valuation/rule/rule-exe-config-update', { title: '规则执行配置详情', dataCode: col.priceRuleExecuteCode, dataName: col.priceRuleExecuteName, }])
    }
}
