import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { RuleBehavior } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-behavior/bean/rule-behavior.bean";
import { specialSymbols,checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'rule-behavior-update',
    templateUrl: './rule-behavior-update.component.html',
    styleUrls: ['./rule-behavior-update.component.scss'],
    providers: [Commfunc]
})
export class RuleBehaviorUpdateComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    data: any;//传入数据
    title: any;//页面标题
    EnAndNumShow: Boolean = false;//规则行为编号校验
    ChineseShow: Boolean = false;//规则行为名称校验
    isClick: Boolean = false;//提交按钮不可连续多次点击控制
    rulebehavior: RuleBehavior = new RuleBehavior();
    constructor(
        public httpService: ValuationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.title = this.route.snapshot.params['title'];
        this.data = this.route.snapshot.params['data'];
        //修改
        if (this.data && this.data != null) {
            this.selectRuleAction();
        }
        this.codeVal();
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //修改初始化
    selectRuleAction() {
        let params = {
            ruleActionCode: this.data
        }
        this.httpService.selectRuleAction(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.rulebehavior = data.productPriceActionType;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '规则行为查询成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则行为查询失败', detail: '调用服务失败！' });
        })
    }
    //汉字校验
    onChineseCheck() {
        if (this.rulebehavior.ruleActionName != '') {
            if (specialSymbols(null, this.rulebehavior.ruleActionName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else{
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.rulebehavior.ruleActionCode != '') {
            if (checkEnAndNum(null, this.rulebehavior.ruleActionCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else{
            this.EnAndNumShow = false;
        }
    }
    //保存
    save() {
        this.isClick = true;
         //规则行为编码不能为空
		if (!this.rulebehavior.ruleActionCode) {
			this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则行为编码不能为空' });
            this.isClick = false;
			return;
        }
         //规则行为名称不能为空
		if (!this.rulebehavior.ruleActionName) {
			this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '规则行为名称不能为空' });
            this.isClick = false;
			return;
        }
        if(this.EnAndNumShow || this.ChineseShow){
            this.isClick = false;
            return;
        }
        let params = {
            ruleActionCode: this.rulebehavior.ruleActionCode,// 规则行为编码
            ruleActionName: this.rulebehavior.ruleActionName,// 规则行为名称
        }
        if (this.title == '规则行为新增') {
            this.httpService.createRuleAction(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '规则行为新增成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-rule-behavior',{query:'1'}])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '规则行为新增失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为新增失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        } else if (this.title == '规则行为修改') {
            this.httpService.updateRuleAction(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '规则行为修改成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-rule-behavior',{query:'1'}])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '规则行为修改失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为修改失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        }

    }
    //返回
    cancel() {
        this.router.navigate(['/pages/tzb/valuation/rule/price-rule-behavior',{query:'1'}])
    }
}
