import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { RuleBehavior } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-behavior/bean/rule-behavior.bean";
import { specialSymbols,checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'rule-behavior',
    templateUrl: './rule-behavior.component.html',
    styleUrls: ['./rule-behavior.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class RuleBehaviorComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    list_Table: any = [];//查询列表
    index: any = '0';//判断是否初始化查询
    EnAndNumShow: Boolean = false;//规则行为编号校验
    ChineseShow: Boolean = false;//规则行为名称校验
    rulebehavior: RuleBehavior = new RuleBehavior();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.codeVal();
        this.index = this.route.snapshot.params['query'];
        if(this.index){
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //汉字校验
    onChineseCheck() {
        if (this.rulebehavior.ruleActionName != '') {
            if (specialSymbols(null, this.rulebehavior.ruleActionName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else{
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.rulebehavior.ruleActionCode != '') {
            if (checkEnAndNum(null, this.rulebehavior.ruleActionCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else{
            this.EnAndNumShow = false;
        }
    }
    //查询
    query() {
        if(this.EnAndNumShow || this.ChineseShow){
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectRuleActionList();
    }
    selectRuleActionList(){
        let params = {
            ruleActionCode: this.rulebehavior.ruleActionCode,// 规则行为编码
            ruleActionName: this.rulebehavior.ruleActionName,// 规则行为名称
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectRuleActionList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                 this.list_Table = data.productPriceActionTypeList;
                 this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '规则行为查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '规则行为查询失败', detail: '调用服务失败！' });
            })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectRuleActionList();
    }
    //重置
    reset(){
        this.rulebehavior = new RuleBehavior();
        this.EnAndNumShow = false;
        this.ChineseShow = false;
        this.query();
    }
    //新增
    addBtn(){
        this.router.navigate(['/pages/tzb/valuation/rule/rule-behavior-update',{ title: '规则行为新增'}])
    }
    //修改
    toUpdate(col){
        this.router.navigate(['/pages/tzb/valuation/rule/rule-behavior-update',{ title: '规则行为修改', data: col.ruleActionCode }])
    }
    //详情
    toDetail(col){
        this.router.navigate(['/pages/tzb/valuation/rule/rule-behavior-update',{ title: '规则行为详情', data: col.ruleActionCode }])
    }
    //删除
    toDelete(col){
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    ruleActionCode: col.ruleActionCode,
                };
                this.httpService.removeRuleAction(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
}
