import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { ConDimension } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/con-dimension/bean/con-dimension.bean";
import { specialSymbols, checkEnAndNum } from "app/@uisftech/common/service/validator";

@Component({
    selector: 'con-dimension',
    templateUrl: './con-dimension.component.html',
    styleUrls: ['./con-dimension.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class ConDimensionComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    list_Table: any = [];//查询数据
    dataSourceSysCode: any = [];//取数来源外系统名称
    index: any = '0';//判断是否初始化查询
    EnAndNumShow: Boolean = false;//条件维度编号校验
    ChineseShow: Boolean = false;//条件维度名称校验
    condimension: ConDimension = new ConDimension();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.codeVal();
        this.selectProductCondValueList();
        this.index = this.route.snapshot.params['query'];
        if (this.index) {
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //取数来源外系统名称
    selectProductCondValueList() {
        let params = {
            conditionalDimensionCode: '000001'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.dataSourceSysCode = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '取数来源外系统名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '取数来源外系统名称查询失败', detail: '调用服务失败！' });
        })
    }
    //查询
    query() {
        if (this.EnAndNumShow || this.ChineseShow) {
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectCondDimensionKindList();
    }
    selectCondDimensionKindList() {
        let params = {
            condDimensionName: this.condimension.condDimensionName,// 条件维度名称
            condDimensionCode: this.condimension.condDimensionCode,// 条件维度编码
            condDimensionDesc: this.condimension.condDimensionDesc,// 条件维度描述
            dataSource: this.condimension.dataSource,//取数来源外系统名称
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectCondDimensionKindList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.condDimensionKindList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度种类查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '条件维度种类查询失败', detail: '调用服务失败！' });
        })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectCondDimensionKindList();
    }
    //汉字校验
    onChineseCheck() {
        if (this.condimension.condDimensionName != '') {
            if (specialSymbols(null, this.condimension.condDimensionName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else {
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.condimension.condDimensionCode != '') {
            if (checkEnAndNum(null, this.condimension.condDimensionCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else {
            this.EnAndNumShow = false;
        }
    }
    //重置
    reset() {
        this.condimension = new ConDimension();
        this.EnAndNumShow = false;
        this.ChineseShow = false;
        this.query();
    }
    //新增
    addBtn() {
        this.router.navigate(['/pages/tzb/valuation/rule/con-dimension-update', { title: '条件维度种类新增' }])
    }
    //修改
    toUpdate(col) {
        this.router.navigate(['/pages/tzb/valuation/rule/con-dimension-update', { title: '条件维度种类修改', data: col.condDimensionCode }])
    }
    toDetail(col) {
        this.router.navigate(['/pages/tzb/valuation/rule/con-dimension-update', { title: '条件维度种类详情', data: col.condDimensionCode }])
    }
    //删除
    toDelete(col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    condDimensionCode: col.condDimensionCode,
                };
                this.httpService.removeCondDimensionKind(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
}
