import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import * as _ from 'lodash';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { ConDimension } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/con-dimension/bean/con-dimension.bean";
import { specialSymbols, checkEnAndNum } from "app/@uisftech/common/service/validator";
@Component({
    selector: 'con-dimension-update',
    templateUrl: './con-dimension-update.component.html',
    styleUrls: ['./con-dimension-update.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class ConDimensionUpdateComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    EnAndNumShow: Boolean = false;//条件维度编号校验
    ChineseShow: Boolean = false;//条件维度名称校验
    isClick: Boolean = false;//控制不可连续多次提交
    data: any;//传入数据
    title: any;//页面标题
    list_Table: any = [];//表格数据展示
    condDimensionValueInfoList: any = [];//表格数据
    dataSourceSysCode: any = [];//取数来源外系统码值
    dataSource: any = [];//取数来源码值
    isQuoteFlag: any;//是否被引用
    condimension: ConDimension = new ConDimension();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {}

    ngOnInit() {
        this.title = this.route.snapshot.params['title'];
        this.data = this.route.snapshot.params['data'];
        //修改
        if (this.data && this.data != null) {
            this.selectCondDimensionKind();
        }
        this.codeVal();
        this.selectProductCondValueList();
        this.dataSource = [{ label: '服务取数', value: '1' }];
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //取数来源外系统名称
    selectProductCondValueList() {
        let params = {
            conditionalDimensionCode: '000001'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.dataSourceSysCode = List;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '取数来源外系统名称查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '取数来源外系统名称查询失败', detail: '调用服务失败！' });
        })
    }
    //修改初始化
    selectCondDimensionKind() {
        let params = {
            condDimensionCode: this.data
        }
        this.httpService.selectCondDimensionKind(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.condimension = data.condDimension;
                this.isQuoteFlag = data.condDimension.isQuoteFlag;
                this.condDimensionValueInfoList = data.condDimensionValueInfoList;
                this.total = this.condDimensionValueInfoList.length;
                this.paginate({ page: 0, first: 0, rows: "10", pageCount: Math.ceil(this.total / 10) });
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '条件维度种类查询成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度种类查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '条件维度种类查询失败', detail: '调用服务失败！' });
        })
    }
    // 保存
    save() {
        this.isClick = true;
        //条件维度名称不能为空
        if (!this.condimension.condDimensionName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '条件维度名称不能为空' });
            this.isClick = false;
            return;
        }
        //条件维度编码不能为空
        if (!this.condimension.condDimensionCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '条件维度编码不能为空' });
            this.isClick = false;
            return;
        }
        if(this.EnAndNumShow || this.ChineseShow){
            this.isClick = false;
            return;
        }
        let params = {
            condDimensionName: this.condimension.condDimensionName,// 条件维度名称
            condDimensionCode: this.condimension.condDimensionCode,// 条件维度编码
            condDimensionDesc: this.condimension.condDimensionDesc,// 条件维度描述
            dataSource: this.condimension.dataSource,//取数来源外系统名称
            dataService: this.condimension.dataService,//服务接口
            condDimensionValueInfoList: this.condDimensionValueInfoList
        }
        if (this.title == '条件维度种类新增') {
            this.httpService.createCondDimensionKind(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '条件维度种类新增成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-con-dimension', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '条件维度种类新增失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度种类新增失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        } else if (this.title == '条件维度种类修改') {
            this.httpService.updateCondDimensionKind(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '条件维度种类修改成功', detail: data.returnCode.message });
                    this.router.navigate(['/pages/tzb/valuation/rule/price-con-dimension', { query: '1' }])
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '条件维度种类修改失败', detail: data.returnCode.message });
                    this.isClick = false;
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '条件维度种类修改失败', detail: '调用服务失败！' });
                this.isClick = false;
            })
        }

    }
    //汉字校验
    onChineseCheck() {
        if (this.condimension.condDimensionName != '') {
            if (specialSymbols(null, this.condimension.condDimensionName)) {
                this.ChineseShow = true;
            } else {
                this.ChineseShow = false;
            }
        } else {
            this.ChineseShow = false;
        }
    }
    //数字和英文校验
    onEnAndNumCheck() {
        if (this.condimension.condDimensionCode != '') {
            if (checkEnAndNum(null, this.condimension.condDimensionCode)) {
                this.EnAndNumShow = true;
            } else {
                this.EnAndNumShow = false;
            }
        } else {
            this.EnAndNumShow = false;
        }
    }
    //翻页
    paginate(event) {
        console.log(event);
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.list_Table = this.condDimensionValueInfoList.slice(this.first, this.first + 10);
    }
    //增加一条
    addBtn() {
        if (this.condDimensionValueInfoList == null) {
            this.condDimensionValueInfoList = [];
        }
        let aa = _.clone(this.condDimensionValueInfoList);
        aa.unshift({
            condDimensionValueCode: '', condDimensionValue: '', condDimensionValueDesc: ''
        });
        this.total++;
        this.condDimensionValueInfoList = aa;
        this.paginate({ page: 0, first: 0, rows: "10", pageCount: Math.ceil(this.total / 10) });
    }
    //删除一条
    toDelete(i, col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                if (col.enumId && col.enumId != null) {
                    let params = {
                        enumId: col.enumId
                    }
                    this.httpService.removeCondDimensionKindValue(params).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            this.condDimensionValueInfoList.splice(this.first + i, 1);
                            this.total--;
                            this.condDimensionValueInfoList = _.clone(this.condDimensionValueInfoList);
                            console.log(this.condDimensionValueInfoList);
                            if (this.condDimensionValueInfoList.slice(this.first, this.first + 10).length != 0) {
                                this.list_Table = this.condDimensionValueInfoList.slice(this.first, this.first + 10);
                                console.log(this.list_Table);
                            } else {
                                this.paginate({ page: this.total / 10 - 1, first: this.total - 10, rows: "10", pageCount: this.total / 10 });
                            }
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '条件维度种类删除成功', detail: data.returnCode.message });
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: '调用服务失败！' });
                    })
                } else {
                    this.condDimensionValueInfoList.splice(this.first + i, 1);
                    this.total--;
                    this.condDimensionValueInfoList = _.clone(this.condDimensionValueInfoList);
                    console.log(this.condDimensionValueInfoList);
                    if (this.condDimensionValueInfoList.slice(this.first, this.first + 10).length != 0) {
                        this.list_Table = this.condDimensionValueInfoList.slice(this.first, this.first + 10);
                        console.log(this.list_Table);
                    } else {
                        this.paginate({ page: this.total / 10 - 1, first: this.total - 10, rows: "10", pageCount: this.total / 10 });
                    }
                }
            }
        });
    }
    //返回
    cancel() {
        this.router.navigate(['/pages/tzb/valuation/rule/price-con-dimension', { query: '1' }])
    }
}
