import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { ConDimensionComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/con-dimension/con-dimension.component";
import { RuleBehaviorUpdateComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-behavior/rule-behavior-update/rule-behavior-update.component";
import { RuleBehaviorComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-behavior/rule-behavior.component";
import { ConDimensionUpdateComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/con-dimension/con-dimension-update/con-dimension-update.component";
import { RuleExeConfigComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-exe-config/rule-exe-config.component";
import { RuleExeConfigUpdateComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/rule-exe-config/rule-exe-config-update/rule-exe-config-update.component";
import { ProductRuleComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/product-rule/product-rule.component";
import { ProductRuleDetailComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-rule/price-rule/product-rule/product-rule-detail/product-rule-detail.component";

export const routes: Routes = [
    {
        path: '',

        children: [
            { path: '', redirectTo: 'rule', pathMatch: 'full' },
            { path: 'price-con-dimension', component: ConDimensionComponent },
            { path: 'rule-behavior-update', component: RuleBehaviorUpdateComponent },
            { path: 'price-rule-behavior', component: RuleBehaviorComponent },
            { path: 'con-dimension-update', component: ConDimensionUpdateComponent },
            { path: 'price-rule-exe-config', component: RuleExeConfigComponent },
            { path: 'rule-exe-config-update', component: RuleExeConfigUpdateComponent },
            { path: 'product-rule', component: ProductRuleComponent },
            { path: 'product-rule-detail', component: ProductRuleDetailComponent },
        ]
    },
]
export const RuleRouting: ModuleWithProviders = RouterModule.forChild(routes);