import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
@Component({
    selector: 'product-rule-detail',
    templateUrl: './product-rule-detail.component.html',
    styleUrls: ['./product-rule-detail.component.scss'],
    providers: [Commfunc]
})
export class ProductRuleDetailComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    user: any;
    orgId: string = '';
    userId: string = '';
    list_Table: any = [];
    detailTable: any = [];
    startDate: Date = null;
    endDate: Date = null;
    addPage: boolean = false;
    personPage: boolean = false;//创建人选择
    personName: string;//创建人
    listOptions: any = [];//清单状态
    dataSourceOptions: any = [];//数据来源
    queryUserInValue: any;
    //选择数据
    chooseData: any = [];
    createUserName: any;
    display: boolean = false;
    isAll: boolean = false;
    checkAll: boolean = false;
    orgTreeDisplay2: boolean = false;
    addHeader = '选择字段';
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        // this.list_Table = [
        //     { custNo: '11' }
        // ]
        this.codeVal();
        this.listOptions = this.code['Source_stat'];//清单状态
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    perShow(){

    }
    cancel(){
        this.router.navigate(['/pages/tzb/valuation/rule/product-rule'])
    }
}
